/* Magazine Container */
.menu-page {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: calc(100vh - 200px);
  padding: 2rem;
}

.magazine-wrapper {
  max-width: 1400px;
  width: 100%;
  perspective: 2000px;
  position: relative;
}

/* Page Styling */
.page-spread {
  display: none;
  gap: 0;
  background: transparent;
  position: relative;
  min-height: 800px;
}

.page-spread.active {
  display: flex;
}

.page {
  flex: 1;
  background: linear-gradient(135deg, #e8f5e9 0%, #c8e6c9 100%);
  padding: 60px 50px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3);
  position: relative;
  overflow: hidden;
  min-height: 800px;
}

.page-left {
  border-radius: 15px 0 0 15px;
  box-shadow: inset -5px 0 15px rgba(0, 0, 0, 0.1);
  background-image: 
    linear-gradient(135deg, #e8f5e9 0%, #c8e6c9 100%),
    repeating-linear-gradient(45deg, transparent, transparent 10px, rgba(76, 175, 80, 0.03) 10px, rgba(76, 175, 80, 0.03) 20px);
}

.page-right {
  border-radius: 0 15px 15px 0;
  box-shadow: inset 5px 0 15px rgba(0, 0, 0, 0.1);
  background-image: 
    linear-gradient(135deg, #e8f5e9 0%, #c8e6c9 100%),
    repeating-linear-gradient(45deg, transparent, transparent 10px, rgba(76, 175, 80, 0.03) 10px, rgba(76, 175, 80, 0.03) 20px);
}

/* Center Binding Effect */
.page::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  width: 1px;
  background: linear-gradient(to bottom, 
    transparent 0%, 
    rgba(44, 24, 16, 0.1) 10%, 
    rgba(44, 24, 16, 0.2) 50%, 
    rgba(44, 24, 16, 0.1) 90%, 
    transparent 100%);
}

.page-left::before {
  right: 0;
}

.page-right::before {
  left: 0;
}

/* Page Numbers */
.page-number {
  position: absolute;
  bottom: 30px;
  font-family: 'Cormorant Garamond', serif;
  font-size: 0.9rem;
  color: var(--text-light);
  font-weight: 300;
}

.page-left .page-number {
  left: 50px;
}

.page-right .page-number {
  right: 50px;
}

/* Header Styling */
.page-header {
  text-align: center;
  margin-bottom: 50px;
  padding-bottom: 20px;
  border-bottom: 2px solid var(--primary);
  position: relative;
}

.page-title {
  font-family: 'Cormorant Garamond', serif;
  font-size: 3rem;
  color: var(--primary);
  font-weight: 400;
  font-style: italic;
  margin-bottom: 10px;
  letter-spacing: 2px;
}

.page-subtitle {
  font-family: 'Montserrat', sans-serif;
  font-size: 0.85rem;
  color: var(--text-light);
  letter-spacing: 4px;
  text-transform: uppercase;
  font-weight: 300;
}

/* Decorative Elements */
.page-decoration {
  position: absolute;
  opacity: 0.05;
  font-size: 200px;
  pointer-events: none;
  z-index: 0;
}

.deco-top-left {
  top: -50px;
  left: -50px;
  transform: rotate(-15deg);
}

.deco-bottom-right {
  bottom: -50px;
  right: -50px;
  transform: rotate(15deg);
}

/* Cover Page Special Styling */
.cover-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #00897b 0%, #00695c 50%, #004d40 100%);
  color: var(--white);
  position: relative;
  overflow: hidden;
}

.cover-page::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: 
    radial-gradient(circle at 20% 30%, rgba(255, 255, 255, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(255, 255, 255, 0.1) 0%, transparent 50%);
}

.cover-content {
  position: relative;
  z-index: 1;
  text-align: center;
}

.cover-logo {
  font-family: 'Cormorant Garamond', serif;
  font-size: 5rem;
  font-weight: 300;
  font-style: italic;
  margin-bottom: 20px;
  letter-spacing: 3px;
  text-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
}

.cover-divider {
  width: 100px;
  height: 2px;
  background: var(--white);
  margin: 0 auto 20px;
  position: relative;
}

.cover-divider::before,
.cover-divider::after {
  content: '';
  position: absolute;
  width: 8px;
  height: 8px;
  background: var(--white);
  border-radius: 50%;
  top: -3px;
}

.cover-divider::before { left: -15px; }
.cover-divider::after { right: -15px; }

.cover-subtitle {
  font-size: 1rem;
  letter-spacing: 6px;
  text-transform: uppercase;
  font-weight: 300;
  margin-bottom: 30px;
  opacity: 0.9;
}

.cover-tagline {
  font-family: 'Cormorant Garamond', serif;
  font-size: 1.5rem;
  font-weight: 300;
  font-style: italic;
  max-width: 400px;
}

/* Welcome Section */
.welcome-section {
  text-align: center;
  margin-bottom: 30px;
}

.welcome-text {
  font-family: 'Cormorant Garamond', serif;
  font-size: 1.3rem;
  color: var(--text-dark);
  margin-bottom: 15px;
  font-style: italic;
}

.welcome-subtext {
  font-size: 0.9rem;
  color: var(--text-light);
}

/* Category Navigation */
.category-nav {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 12px;
  margin-top: 30px;
}

.category-btn {
  background: linear-gradient(135deg, rgba(0, 137, 123, 0.1) 0%, rgba(0, 105, 92, 0.1) 100%);
  border: 2px solid #00897b;
  color: var(--text-dark);
  padding: 15px 10px;
  border-radius: 12px;
  font-family: 'Montserrat', sans-serif;
  font-size: 0.85rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  text-align: center;
}

.category-btn .icon {
  font-size: 1.8rem;
}

.category-btn:hover {
  background: linear-gradient(135deg, #00897b 0%, #00695c 100%);
  color: white;
  transform: translateY(-3px);
  box-shadow: 0 5px 15px rgba(0, 137, 123, 0.3);
}

.category-btn:active {
  transform: translateY(-1px);
}

/* Tip Section */
.tip-section {
  margin-top: 40px;
  text-align: center;
  padding: 20px;
  background: rgba(0, 137, 123, 0.05);
  border-radius: 10px;
  border: 1px solid rgba(0, 137, 123, 0.2);
}

.tip-section p {
  font-size: 0.85rem;
  color: var(--text-light);
  line-height: 1.6;
}

/* Menu Section Styling */
.menu-section {
  position: relative;
  z-index: 1;
  margin-bottom: 40px;
}

.section-header {
  display: flex;
  align-items: center;
  margin-bottom: 25px;
  padding-bottom: 10px;
  border-bottom: 1px solid rgba(193, 124, 74, 0.3);
}

.section-icon {
  font-size: 1.8rem;
  margin-right: 12px;
}

.section-title {
  font-family: 'Cormorant Garamond', serif;
  font-size: 1.6rem;
  font-weight: 600;
  color: var(--text-dark);
  letter-spacing: 1px;
}

.menu-item {
  margin-bottom: 20px;
  position: relative;
  padding-left: 12px;
}

.menu-item::before {
  content: 'â€¢';
  position: absolute;
  left: 0;
  top: 0;
  color: var(--primary);
  font-size: 1.2rem;
}

.item-header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  gap: 10px;
  margin-bottom: 3px;
}

.item-name {
  font-family: 'Montserrat', sans-serif;
  font-size: 0.95rem;
  font-weight: 500;
  color: var(--text-dark);
  flex: 0 1 auto;
}

.item-dots {
  flex: 1 1 auto;
  border-bottom: 1px dotted rgba(44, 24, 16, 0.25);
  margin: 0 8px 4px 8px;
  min-width: 15px;
}

.item-price {
  font-family: 'Montserrat', sans-serif;
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--primary);
  white-space: nowrap;
  flex: 0 0 auto;
}

.item-description {
  font-size: 0.8rem;
  color: var(--text-light);
  font-weight: 300;
  line-height: 1.4;
  font-style: italic;
  margin-top: 2px;
}

/* Final Section */
.final-section {
  margin-top: 80px;
}

.welcome-box {
  text-align: center;
  padding: 40px;
  border: 2px solid var(--primary);
  border-radius: 15px;
  background: linear-gradient(135deg, rgba(193, 124, 74, 0.05), rgba(139, 90, 60, 0.05));
}

.welcome-title {
  font-family: 'Cormorant Garamond', serif;
  font-size: 2rem;
  color: var(--primary);
  margin-bottom: 15px;
  font-style: italic;
}

.divider {
  width: 60px;
  height: 2px;
  background: var(--primary);
  margin: 0 auto 15px;
}

.website {
  font-size: 0.95rem;
  color: var(--text-dark);
  margin-bottom: 10px;
  font-weight: 500;
}

.tagline {
  font-size: 0.85rem;
  color: var(--text-light);
  font-style: italic;
}

/* Navigation */
.navigation {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 40px;
}

.nav-btn {
  background: linear-gradient(135deg, #00897b 0%, #00695c 100%);
  color: var(--white);
  border: none;
  padding: 12px 30px;
  font-family: 'Montserrat', sans-serif;
  font-size: 0.9rem;
  font-weight: 500;
  letter-spacing: 2px;
  text-transform: uppercase;
  cursor: pointer;
  border-radius: 30px;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 137, 123, 0.4);
}

.nav-btn:hover:not(:disabled) {
  background: linear-gradient(135deg, #00695c 0%, #004d40 100%);
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 137, 123, 0.5);
}

.nav-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

.page-indicator {
  text-align: center;
  margin-top: 20px;
  font-family: 'Montserrat', sans-serif;
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.7);
  letter-spacing: 1px;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .magazine-wrapper {
    max-width: 100%;
  }
  
  .page {
    padding: 40px 30px;
    min-height: 700px;
  }
  
  .page-title {
    font-size: 2.5rem;
  }
  
  .cover-logo {
    font-size: 4rem;
  }
}

@media (max-width: 968px) {
  .menu-page {
    padding: 1rem;
  }
  
  .page-spread {
    flex-direction: column;
    min-height: auto;
  }
  
  .page {
    display: none;
    border-radius: 15px;
    min-height: 600px;
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.15);
    margin-bottom: 1rem;
  }
  
  .page-spread.active .page {
    display: block;
  }
  
  .page-left,
  .page-right {
    border-radius: 15px;
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.15);
  }
  
  .page::before {
    display: none;
  }
  
  .page-number {
    text-align: center;
    left: 50%;
    right: auto;
    transform: translateX(-50%);
  }
  
  .page-right .page-number {
    left: 50%;
    right: auto;
    transform: translateX(-50%);
  }
  
  .category-nav {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 640px) {
  .menu-page {
    padding: 0.5rem;
  }
  
  .page {
    padding: 30px 20px;
    min-height: 500px;
  }
  
  .page-title {
    font-size: 2rem;
  }
  
  .cover-logo {
    font-size: 3rem;
  }
  
  .section-title {
    font-size: 1.3rem;
  }
  
  .item-name {
    font-size: 0.85rem;
  }
  
  .item-price {
    font-size: 0.8rem;
  }
  
  .nav-btn {
    padding: 10px 20px;
    font-size: 0.8rem;
  }
  
  .category-nav {
    grid-template-columns: repeat(2, 1fr);
    gap: 8px;
  }
  
  .category-btn {
    padding: 12px 8px;
    font-size: 0.8rem;
  }
}
